<!-- 教程 -->
<template>
  <div class="container">
    <van-nav-bar left-arrow fixed title="查询结果教程" @click-left="handleBack">
      <template #right>
        <img src="../assets/images/kf.png" />
      </template>
    </van-nav-bar>
    <van-tabs v-model:active="active" :line-height="0">
      <van-tab :title="item.title" v-for="(item, index) in datalist" :key="index">
        <a class="btn-link" :href="item.link" v-if="item.link" target="_blank"> 进入{{ item.label }}查询</a>
        <img :src="item.image" v-if="item.image" />
        <div v-else class="prompt">平台内进入任意游戏 输入钱包地址 点击一键查询即可</div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: 'Tutorial',
  data() {
    return {
      active: 0,
      datalist: [
        { title: 'tronscan', label: "tronscan.io", link: 'https://tronscan.io/#/', image: 'https://q.yb-visa.com/static/media/tronscan.fec6aaff.jpg' },
        { title: 'tokenview', label: "tokenview.com", link: 'https://tokenview.com/cn', image: 'https://q.yb-visa.com/static/media/tokenview.1c757f22.jpg' },
        { title: '钱包查询', label: "", link: '', image: 'https://q.yb-visa.com/static/media/wallet.48b0e0b2.jpg' },
        { title: '本站查询', label: "", link: '', image: '' },
      ]
    }
  },
  created() {
    let { id } = this.$route.query;
    if (id) this.active = + id;
  },
  methods: {
    handleBack() {
      this.$router.back();
    }
  }
}
</script>
<style lang='scss' scoped>
.container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 50px 10px 10px;
  background-color: #f5f8ff;
}

.van-tabs {
  :deep(.van-tabs__wrap) {
    height: 36px;

    .van-tabs__nav {
      background-color: #d9e6ff;

      .van-tab {
        font-size: 12px;
      }

      .van-tab--active {
        color: #0648b4;
        border-radius: 8px 8px 0 0;
        background-color: #fff;
      }
    }
  }

  :deep(.van-tabs__content) {
    padding: 10px 0;

    .btn-link {
      width: 100%;
      padding: 12px 0;
      border-radius: 6px;
      display: inline-block;
      text-align: center;
      color: var(--main-color);
      margin-bottom: 10px;
      background-color: #e9f0ff;
    }

    img {
      max-width: 100%;
    }
    .prompt{
      text-align: center;
    }
  }
}
</style>